@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');
:root {
  --headerNavColor: #1E2328;
  --bodyColor: #2A2E34;
  --tableColor: #3B3F46;
  --yellowDark: #F5A302;
  --yellow: #F5B301;
  --yellowLight: #FED053;
  --fontColor: #FBF9ED;
  --alert: #A64452;
}

body {
  background-color: var(--bodyColor);
  margin: 0;
  padding: 0;
  color: var(--fontColor);
  font-family: 'Roboto Condensed', sans-serif;
}

nav {
  background-color: var(--headerNavColor);
  padding: 1em;
  display: grid;
  align-items: center;
  grid-template-columns: 2fr 1fr 1fr;
}

.link {
  color: var(--fontColor);
  text-decoration: none;
  font-size: 25px;
  margin-inline: 1em;
  transition: color 0.4s ease-in-out;
}

.link:hover {
  color: var(--yellowLight);
  transition: color 0.4s ease-in-out;
}

.link:nth-of-type(1) {
  justify-self: left;
}

.link:nth-of-type(2) {
  justify-self: right;
  align-self: right;
}

.link:nth-of-type(3) {
  justify-self: right;
  align-self: right;
}

/* Modal for login and session creation */

.modal {
  width: fit-content;
  padding: 1em;
  height: auto;
  position: absolute;
  margin-inline: auto;
  left: 0;
  right: 0;
  text-align: center;
  background-color: var(--tableColor);
  border-radius: 13px;
  margin-top: 3em;
}

.modalLogin {
  padding: 3em;
}

form {
  text-align: left;
  margin: 1em;
}

.group {
  position: relative;
  margin-bottom: 45px;
}

input {
  padding: 10px 10px 10px 5px;
  display: block;
  width: 285px;
  border: none;
  background-color: var(--tableColor);
  border-bottom: 1px solid var(--yellowDark);
  color: var(--fontColor);
}

input:focus {
  outline: none;
}

label {
  color: #999;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

input:focus ~ label, input:valid ~ label {
  top:-20px;
  font-size: 14px;
  color: var(--fontColor);
}

.bar {
  position: relative;
  display: block;
  width: 300px;
}

.bar:before, .bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: var(--yellowDark);
  transition: 0.2s ease all;
  -moz-transition:0.2s ease all; 
  -webkit-transition:0.2s ease all;
}

.bar:before {
  left: 50%;
}

.bar:after {
  right: 50%;
}

input:focus ~ .bar:before, input:focus ~ .bar:after {
  width: 50%;
}

.dateInput {
  color: var(--fontColor);
}

.submitInput {
  color: var(--fontColor);
  font-size: 18px;
  border-top: 1px solid var(--yellowDark);
  border-bottom: 1px solid var(--yellowDark);
  margin-top: 2em;
  background-color: transparent;
  border-inline: none;
  margin-bottom: 1em;
  width: 80%;
  line-height: 1.69em;
}

.submitInput:hover {
  color: var(--yellowLight);
}

/* Home Page */

.post {
  background-color: var(--tableColor);
  width: max-content;
  height: max-content;
  padding: 2em;
  position: absolute;
  margin-inline: auto;
  left: 0;
  right: 0;
  border-radius: 13px; 
  margin-top: 2em;
}

.postHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.deletePostDiv {
  margin-left: 50%;
  color: var(--tableColor);
}

.deletePostDiv button {
  color: inherit;
  background-color: var(--yellow);
  border: none;
  border-radius: 2px;
  width: 2em;
  height: 2em;
  display: grid;
  place-items: center;
}

.deletePostDiv button:hover {
  filter: grayscale(.35);
}

.deletePostDiv abbr {
  text-decoration: none;
}

/* Nav */

.signOutBtn {
  border-radius: 13px;
  background-color: var(--headerNavColor);
  width: fit-content;
  height: max-content;
  padding-inline: 1em;
  padding-top: .25em;
  padding-bottom: .25em;
  border: 2px solid var(--fontColor);
  justify-self: right;
  margin-right: 2em;
  font-size: 25px;
  color: var(--fontColor);
  transition: background-color 0.5s ease-in-out, border 0.5s ease-in-out;
}

.signOutBtn:hover {
  background-color: var(--yellow);
  border: 2px solid transparent;
  transition: background-color 0.5s ease-in-out, border 0.5s ease-in-out;
}